import { Dialog ,Box,Typography,styled, InputBase, TextField,Button} from "@mui/material"
import { Close,DeleteOutline } from "@mui/icons-material"
import { useState } from "react"
import useApi from "../hooks/useApi"
import { API_URLS } from "../services/api.urls"


const dialogStyle={
    height:'90%',
    width:'80%',
    maxWidth:'100%',
    maxHeight:'100%',
    boxShadow:'none',
    borderRadius:'10px 10px 0 0',
    overflow:'hidden'
}

const Header =styled(Box)({
    display:'flex',
    justifyContent:'space-between',
    padding:'10px 15px',
    background:'#f2f6fc',
    '& > p':{
        fontSize:14,
        fontWeight:500

    }
})

const Footer=styled(Box)({
  display:'flex',
  justifyContent:'space-between',
  padding:'10px 15px',
  alignItems:'center'
})

const SendButton=styled(Button)({
    background:'#0B57D0',
    color: '#fff',
    fontWeight:500,
    textTransform:'none',
    borderRadius:18,
    width:100
})

const RecipientsWrapper=styled(Box)({
    display:'flex',
    flexDirection:'column',
    padding: '0 16px',
    '& > div':{
        fontSize:14,
        borderBottom: ' 1px solid #F5F5F5',
        marginTop:'10px'
    }
})

const config={
    Host : "smtp.elasticemail.com",
    Username : "testing123@yopmail.com",
    Password : "3C87C73BD659F2D934E720F5B81E1791F7DA",
    Port:2525
}



const ComposeMail= ({composeDialog,setComposeDialog})=>{
    const [data,setData]=useState({})
    const sentEmailServices=useApi(API_URLS.saveSentEmails)
    const saveDraftServices=useApi(API_URLS.saveDraftEmails)
    function handleChange(e){
        setData({...data,[e.target.name]:e.target.value})
    }

    const saveDraft=async(e)=>{
         e.preventDefault();
          if(data.to===undefined ){
            setComposeDialog(false);
            setData({})
            return;
          }
         const payload={
            to : data.to,
            from : "chaudharyg15112001@gmail.com",
            subject : data.subject,
            body : data.body,
            date: new Date(),
            image: '',
            name: 'Code for Interview',
            starred: false,
            type:'drafts'
        }

        saveDraftServices.call(payload)
        if(!sentEmailServices.error)
        {
           setComposeDialog(false);
           setData({})
        }

    }

    const sendEmail = async (e) => {
        e.preventDefault();

        if (window.Email) {
            window.Email.send({
                ...config,
                To : data.to,
                From : "chaudharyg15112001@gmail.com",
                Subject : data.subject,
                Body : data.body
            }).then(
                message => alert(message)
            );
        }

        const payload={
            to : data.to,
            from : "chaudharyg15112001@gmail.com",
            subject : data.subject,
            body : data.body,
            date: new Date(),
            image: '',
            name: 'Code for Interview',
            starred: false,
            type: 'sent'
        }

        sentEmailServices.call(payload);

         if(!sentEmailServices.error)
         {
            setComposeDialog(false);
            setData({})
         }

      
    }

    return(
        <Dialog
          
         open={composeDialog}
         PaperProps={{
            sx:dialogStyle 
         }}

        >
            <Header>
                 <Typography>New Message</Typography>
                 <Close color="action" fontSize="small" onClick={(e)=>saveDraft(e)}/>
            </Header>
            <RecipientsWrapper>
                  <InputBase placeholder="Recipients" name="to" onChange={(e)=>handleChange(e)}></InputBase>
                  <InputBase placeholder="Subject" name="subject" onChange={(e)=>handleChange(e)}></InputBase>
            </RecipientsWrapper>
            <TextField
              name="body"
              onChange={(e)=>handleChange(e)}
              multiline
              rows={20}
              sx={{'& .MuiOutlinedInput-notchedOutline':{ border:'none'}}}
            >
                
            </TextField>
            <Footer>
                 <SendButton onClick={(e) => sendEmail(e)}>Send</SendButton>
                 <DeleteOutline onClick={()=>{setComposeDialog(false)}}/>
            </Footer>
        </Dialog>
    )
}
export default ComposeMail